<template>
    <section id="main-slider" v-if="slides">
        <hooper ref="carousel" :mouse-drag="false" :touch-drag="false" :short-drag="false" :wheel-control="false" @afterSlide="sliderUpdated" @updated="sliderUpdated">
            <template v-for="(slideData, indexLvl1) in slides">
                <slide :key="indexLvl1">
                    <div class="main-slider-item"

                         :style="{ backgroundImage: `url('/static/${slideData.image}')` }"
                        :class="'type-' + slideData.type">
                        <div class="container-fluid">
                            <div class="row">
                                    <div class="col-lg-6 col-md-8 col-sm-12 col-xs-12">
                                        <div class="h1 relative">
                                            <img v-if="slideData.type == 2" src="../../assets/images/dynamic/images/image-2.png" alt="#" class="smile-1">
                                            <img v-if="slideData.type == 2" src="../../assets/images/dynamic/images/image-4.png" alt="#" class="smile-3">
                                            <img v-if="slideData.type == 2" src='../../assets/images/dynamic/images/image-5.png' alt='#' class='smile-2'>
                                            <img v-if="slideData.type == 3" src="../../assets/images/dynamic/images/image-8.png" alt="#" class="smile-1">
                                            <img v-if="slideData.type == 3" src="../../assets/images/dynamic/images/image-5.png" alt="#" class="smile-3">
                                            <span v-html="slideData.title">
                                            </span>
                                        </div>
                                        <p>{{ slideData.subtitle }}</p>
                                        <div class="buttons">
                                            <div class="btn-box">
                                                <div class="btn btn-1" @click="openBaseModal()">
                                                    Записатися на пробне заняття
                                                </div>
                                            </div>
                                            <div v-if="slideData.type == 1 || slideData.type == 2" class="btn-box">
                                                <div class="btn btn-5">
                                                    <a href="#">Дізнатися свій рівень</a>
                                                </div>
                                            </div>
                                        </div>

                                    </div>
                                    <div class="col-lg-6 col-md-4 hidden-sm hidden-xs">
                                        <img v-if="slideData.logo === true" src="../../assets/images/images.svg#image-1" alt="#" class="logo">
                                    </div>
                                </div>
                            </div>
                        </div>

                </slide>
            </template>
            <hooper-pagination slot="hooper-addons"></hooper-pagination>
        </hooper>
    </section>
</template>

<script>
    import {
        Hooper,
        Slide,
        Pagination as HooperPagination
    } from 'hooper';
    import 'hooper/dist/hooper.css';
    import { mapActions } from 'vuex';
    import {
        mainSlides,
        workshopSlides,
        teenSlides
    } from '../../const/slides';
    export default {
        components: {
            Hooper,
            Slide,
            HooperPagination
        },
        props: ['slidesType'],
        data() {
            return {
                carousel: null,
                data: {},
                mainSlides,
                teenSlides,
                workshopSlides,
                slides: null
            }
        },
        methods: {
            ...mapActions('modals', ['openBaseModal']),
            generateSlides() {
                switch (this.slidesType) {
                    case 'teen': {
                        return this.teenSlides;
                    }
                    case 'workshop': {
                        return this.workshopSlides;
                    }
                    default: {
                        return this.mainSlides;
                    }
                }
            },
            slidePrev() {
                this.$refs.carousel.slidePrev();
            },
            slideNext() {
                this.$refs.carousel.slideNext();
            },
            sliderUpdated() {
                try {
                    document.getElementById('main-slider').getElementsByClassName("hooper")[0].style.height = document.getElementById('main-slider').getElementsByClassName('hooper-slide is-active is-current')[0].getElementsByClassName("slider-item")[0].offsetHeight+"px";
                } catch (e) {
                    console.warn(e);
                }
            }
        },
        created() {
            this.slides = this.generateSlides();
        },
        mounted() {
            this.carousel = this.$refs.carousel;
        }
    }
</script>
